      this.maShow = true;
      this.maC = 'current';
    },
    changeuserShow() {
      this.maC = '';
      this.userShow = true;
      this.maShow = false;
      this.userC = 'current';
    },
    toPost(){
      this.$router.push({
        name:'zhuce'
      })
    },
    loginG(){
      this.axios.post('http://localhost:80/api/login.php?'+`username=${this.username}&password=${this.password}`).then(res=>{
        if(res.data){
          this.$router.push({
            name:'zhuye',
            params:{
              username:this.username
            }
          })          
        }
        else{
          alert('用户名或密码错误!');
        }
      }).catch(err=>{
        console.log(err.messsage);
